      transition: 0.3s;
      -webkit-transition: 0.3s;
      -moz-transition: 0.3s;
      -ms-transition: 0.3s;
      -o-transition: 0.3s;
}

.card:hover {
      box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}
    
